"use client"

import type React from "react"
import { LayoutWrapper } from "@/components/layout-wrapper"
import { ArrowLeft, Edit2, Phone, Mail, MapPin, Calendar, User, FileText } from "lucide-react"
import { useState, useEffect } from "react"
import { useRouter, useParams } from "next/navigation"
import { getCurrentUser, checkPermission } from "@/lib/constants"
import { logActivity } from "@/lib/utils"

// Type definitions
type PatientRole = "student" | "teaching_staff" | "non_teaching_staff"
type Program = "CICT" | "CBME"
type StudentCourse = "BSIT" | "BSCS" | "BSIS" | "BTVTED"
type CBMECourse = "BSA" | "BSAIS" | "BPA" | "BSE"
type NonTeachingCategory = "Administrative" | "Security" | "Maintenance" | "Support" | "Clinic"

interface Patient {
  id: string
  firstName: string
  middleName?: string
  lastName: string
  suffix?: string
  dateOfBirth: string
  gender: "Male" | "Female"
  phone: string
  email?: string
  address?: string
  role: PatientRole
  idNumber: string
  program?: Program
  course?: StudentCourse | CBMECourse
  yearLevel?: number
  block?: number
  department?: Program
  staffCategory?: NonTeachingCategory
  // Medical History
  pastIllnesses?: string
  surgeries?: string
  currentMedication?: string
  allergies?: string
  medicalNotes?: string
  // Emergency Contacts
  primaryContactName?: string
  primaryContactRelationship?: string
  primaryContactPhone?: string
  primaryContactAddress?: string
  secondaryContactName?: string
  secondaryContactRelationship?: string
  secondaryContactPhone?: string
  secondaryContactAddress?: string
  attachments?: string
}

export default function PatientDetailPage() {
  const router = useRouter()
  const params = useParams()
  const patientId = params.id as string

  const [patient, setPatient] = useState<Patient | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [user, setUser] = useState<any>(null)

  useEffect(() => {
    const currentUser = getCurrentUser()
    if (currentUser) setUser(currentUser)
  }, [])

  useEffect(() => {
    if (user && patientId) {
      fetchPatient()
    }
  }, [user, patientId])

  const fetchPatient = async () => {
    if (!user || !patientId) return
    try {
      setIsLoading(true)
      const response = await fetch(`/api/patients/${patientId}`, {
        headers: { "x-user-id": user.id },
      })
      if (response.ok) {
        const data = await response.json()
        setPatient(data.patient)
      } else if (response.status === 404) {
        // Patient not found, redirect to patients list
        router.push('/patients')
      }
    } catch (error) {
      console.error("Error fetching patient:", error)
      router.push('/patients')
    } finally {
      setIsLoading(false)
    }
  }

  const canEditPatients = user && checkPermission(user.role, "canEditPatients")

  const getFullName = (patient: Patient): string => {
    const name = [patient.firstName, patient.middleName, patient.lastName]
      .filter(Boolean)
      .join(" ")
    return patient.suffix ? `${name} ${patient.suffix}` : name
  }

  const getDisplayName = (patient: Patient): string => {
    const middleInitial = patient.middleName ? `${patient.middleName.charAt(0)}. ` : ""
    const suffix = patient.suffix ? ` ${patient.suffix}` : ""
    return `${patient.lastName}, ${patient.firstName} ${middleInitial}${suffix}`.trim()
  }

  const getCourseDisplay = (patient: Patient): string => {
    if (patient.role !== "student" || !patient.course || !patient.yearLevel) return ""
    const courseInitials = patient.course.match(/[A-Z]/g)?.join("") || patient.course
    const block = patient.block ? ` - ${patient.block}` : ""
    return `${courseInitials} ${patient.yearLevel}${block}`
  }

  const getRoleDisplay = (role: PatientRole): string => {
    switch (role) {
      case "student":
        return "Student"
      case "teaching_staff":
        return "Teaching Staff"
      case "non_teaching_staff":
        return "Non-teaching Staff"
    }
  }

  const calculateAge = (dateOfBirth: string): number => {
    const today = new Date()
    const birthDate = new Date(dateOfBirth)
    let age = today.getFullYear() - birthDate.getFullYear()
    const monthDiff = today.getMonth() - birthDate.getMonth()
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birthDate.getDate())) {
      age--
    }
    return age
  }

  if (isLoading) {
    return (
      <LayoutWrapper>
        <div className="flex items-center justify-center min-h-[400px]">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#8B3A3A] mx-auto mb-4"></div>
            <p className="text-gray-600">Loading patient details...</p>
          </div>
        </div>
      </LayoutWrapper>
    )
  }

  if (!patient) {
    return (
      <LayoutWrapper>
        <div className="flex items-center justify-center min-h-[400px]">
          <div className="text-center">
            <h2 className="text-2xl font-bold text-gray-800 mb-2">Patient Not Found</h2>
            <p className="text-gray-600 mb-4">The patient you're looking for doesn't exist.</p>
            <button
              onClick={() => router.push('/patients')}
              className="bg-[#8B3A3A] text-white px-6 py-2 rounded-lg font-semibold hover:bg-[#6D2E2E]"
            >
              Back to Patients
            </button>
          </div>
        </div>
      </LayoutWrapper>
    )
  }

  return (
    <LayoutWrapper>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <button
              onClick={() => router.push('/patients')}
              className="flex items-center gap-2 text-gray-600 hover:text-gray-800 transition"
            >
              <ArrowLeft size={20} />
              Back to Patients
            </button>
            <div>
              <h1 className="text-3xl font-bold text-[#8B3A3A]">{getDisplayName(patient)}</h1>
              <p className="text-gray-600">{getRoleDisplay(patient.role)} â€¢ ID: {patient.idNumber}</p>
            </div>
          </div>
          {canEditPatients && (
            <button
              onClick={() => router.push(`/patients?edit=${patient.id}`)}
              className="bg-[#8B3A3A] text-white px-6 py-3 rounded-lg font-semibold hover:bg-[#6D2E2E] flex items-center gap-2"
            >
              <Edit2 size={20} />
              Edit Patient
            </button>
          )}
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Main Information */}
          <div className="lg:col-span-2 space-y-6">
            {/* Personal Information */}
            <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
              <h2 className="text-xl font-bold text-gray-800 mb-4 flex items-center gap-2">
                <User size={20} className="text-[#8B3A3A]" />
                Personal Information
              </h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold text-gray-600 mb-1">Full Name</label>
                  <p className="text-gray-800">{getFullName(patient)}</p>
                </div>
                <div>
                  <label className="block text-sm font-semibold text-gray-600 mb-1">Gender</label>
                  <p className="text-gray-800">{patient.gender}</p>
                </div>
                <div>
                  <label className="block text-sm font-semibold text-gray-600 mb-1">Date of Birth</label>
                  <p className="text-gray-800">{new Date(patient.dateOfBirth).toLocaleDateString()}</p>
                </div>
                <div>
                  <label className="block text-sm font-semibold text-gray-600 mb-1">Age</label>
                  <p className="text-gray-800">{calculateAge(patient.dateOfBirth)} years old</p>
                </div>
                <div>
                  <label className="block text-sm font-semibold text-gray-600 mb-1">Phone</label>
                  <p className="text-gray-800 flex items-center gap-2">
                    <Phone size={16} />
                    {patient.phone}
                  </p>
                </div>
                {patient.email && (
                  <div>
                    <label className="block text-sm font-semibold text-gray-600 mb-1">Email</label>
                    <p className="text-gray-800 flex items-center gap-2">
                      <Mail size={16} />
                      {patient.email}
                    </p>
                  </div>
                )}
                {patient.address && (
                  <div className="md:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 mb-1">Address</label>
                    <p className="text-gray-800 flex items-center gap-2">
                      <MapPin size={16} />
                      {patient.address}
                    </p>
                  </div>
                )}
              </div>
            </div>

            {/* School Information */}
            <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
              <h2 className="text-xl font-bold text-gray-800 mb-4 flex items-center gap-2">
                <Calendar size={20} className="text-[#8B3A3A]" />
                School Information
              </h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold text-gray-600 mb-1">Role</label>
                  <p className="text-gray-800">{getRoleDisplay(patient.role)}</p>
                </div>
                <div>
                  <label className="block text-sm font-semibold text-gray-600 mb-1">ID Number</label>
                  <p className="text-gray-800">{patient.idNumber}</p>
                </div>
                {patient.role === "student" && (
                  <>
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Program</label>
                      <p className="text-gray-800">{patient.program}</p>
                    </div>
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Course</label>
                      <p className="text-gray-800">{patient.course}</p>
                    </div>
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Year Level</label>
                      <p className="text-gray-800">{patient.yearLevel}</p>
                    </div>
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Block</label>
                      <p className="text-gray-800">{patient.block}</p>
                    </div>
                  </>
                )}
                {patient.role === "teaching_staff" && (
                  <div>
                    <label className="block text-sm font-semibold text-gray-600 mb-1">Department</label>
                    <p className="text-gray-800">{patient.department}</p>
                  </div>
                )}
                {patient.role === "non_teaching_staff" && (
                  <div>
                    <label className="block text-sm font-semibold text-gray-600 mb-1">Category</label>
                    <p className="text-gray-800">{patient.staffCategory}</p>
                  </div>
                )}
              </div>
            </div>

            {/* Medical History */}
            {(patient.pastIllnesses || patient.surgeries || patient.currentMedication || patient.allergies || patient.medicalNotes) && (
              <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                <h2 className="text-xl font-bold text-gray-800 mb-4 flex items-center gap-2">
                  <FileText size={20} className="text-[#8B3A3A]" />
                  Medical History
                </h2>
                <div className="space-y-4">
                  {patient.pastIllnesses && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Past Illnesses</label>
                      <p className="text-gray-800">{patient.pastIllnesses}</p>
                    </div>
                  )}
                  {patient.surgeries && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Surgeries</label>
                      <p className="text-gray-800">{patient.surgeries}</p>
                    </div>
                  )}
                  {patient.currentMedication && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Current Medication</label>
                      <p className="text-gray-800">{patient.currentMedication}</p>
                    </div>
                  )}
                  {patient.allergies && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Allergies</label>
                      <p className="text-gray-800">{patient.allergies}</p>
                    </div>
                  )}
                  {patient.medicalNotes && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-600 mb-1">Additional Notes</label>
                      <p className="text-gray-800">{patient.medicalNotes}</p>
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>

          {/* Emergency Contacts Sidebar */}
          <div className="space-y-6">
            {/* Primary Contact */}
            {(patient.primaryContactName || patient.primaryContactPhone) && (
              <div className="bg-red-50 rounded-xl border border-red-200 p-6">
                <h3 className="text-lg font-bold text-red-800 mb-4">Primary Emergency Contact</h3>
                <div className="space-y-3">
                  {patient.primaryContactName && (
                    <div>
                      <label className="block text-sm font-semibold text-red-700 mb-1">Name</label>
                      <p className="text-red-800">{patient.primaryContactName}</p>
                    </div>
                  )}
                  {patient.primaryContactRelationship && (
                    <div>
                      <label className="block text-sm font-semibold text-red-700 mb-1">Relationship</label>
                      <p className="text-red-800">{patient.primaryContactRelationship}</p>
                    </div>
                  )}
                  {patient.primaryContactPhone && (
                    <div>
                      <label className="block text-sm font-semibold text-red-700 mb-1">Phone</label>
                      <p className="text-red-800 flex items-center gap-2">
                        <Phone size={16} />
                        {patient.primaryContactPhone}
                      </p>
                    </div>
                  )}
                  {patient.primaryContactAddress && (
                    <div>
                      <label className="block text-sm font-semibold text-red-700 mb-1">Address</label>
                      <p className="text-red-800 flex items-center gap-2">
                        <MapPin size={16} />
                        {patient.primaryContactAddress}
                      </p>
                    </div>
                  )}
                </div>
              </div>
            )}

            {/* Secondary Contact */}
            {(patient.secondaryContactName || patient.secondaryContactPhone) && (
              <div className="bg-gray-50 rounded-xl border border-gray-200 p-6">
                <h3 className="text-lg font-bold text-gray-800 mb-4">Secondary Emergency Contact</h3>
                <div className="space-y-3">
                  {patient.secondaryContactName && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-700 mb-1">Name</label>
                      <p className="text-gray-800">{patient.secondaryContactName}</p>
                    </div>
                  )}
                  {patient.secondaryContactRelationship && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-700 mb-1">Relationship</label>
                      <p className="text-gray-800">{patient.secondaryContactRelationship}</p>
                    </div>
                  )}
                  {patient.secondaryContactPhone && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-700 mb-1">Phone</label>
                      <p className="text-gray-800 flex items-center gap-2">
                        <Phone size={16} />
                        {patient.secondaryContactPhone}
                      </p>
                    </div>
                  )}
                  {patient.secondaryContactAddress && (
                    <div>
                      <label className="block text-sm font-semibold text-gray-700 mb-1">Address</label>
                      <p className="text-gray-800 flex items-center gap-2">
                        <MapPin size={16} />
                        {patient.secondaryContactAddress}
                      </p>
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </LayoutWrapper>
  )
}
